.ContactPage {
  background: rgba(0, 0, 0, 0.2) url("/images/contact.jpg") no-repeat fixed;
  background-size: cover;
  padding: 5rem 1rem;
  background-blend-mode: multiply;
  min-height: 100vh;
  display: flex;
  align-items: center;
}
/* .ContactPage::placeholder {
  color: #fff !important;
} */

.contactForm input,
.contactForm textarea {
  background: transparent;
}

.contactInfo {
  text-align: center;
  color: #fff;
}

.ContactPage h1 {
  color: #fff;
}

.span {
  color: gold;
}

p {
  font-weight: 500;
  font-size: 1rem;
}

.contactIcons {
  display: flex;
  justify-content: space-around;
}
.contactIcons .anticon {
  font-size: 2rem;
}
